@import url(https://fonts.googleapis.com/css?family=Cutive+Mono|Raleway&display=swap);
.body {
  width: 86%;
  margin-left: 8%;
  margin-right: 8%;
}

.Header {
  width: 100%;
  min-height: 60px;
  background-color: #fff;
}

.MainBody {
  background-color: #fff;
}

.List {
  margin-top: 30px;
  margin-left: 20px;
  width: 300px;
  padding-left: 15px;
  background-color: #eee;
}

.Content {
  background-color: #00f;
}

.Cart {
  background-color: #0ff;
}
.card-view {
    min-width: 800px;
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
    height: 180px;
    display: flex;
    border-top: 10px black;
}


.card-view:hover {
    box-shadow: 0px 0px 20px #000;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    background-color: #fff;
}

.card-view:active {
    background-color: #aaa;
}

.card-image {
    min-width: 400px;
    max-width: 500px;
    width: 55%;
    height: auto;
}

.card-content {
    margin-left: 5%;
    min-width: 300px;
    width: 80%;
}

.button-div button {
    padding: 10px;
    width: 100px;
    background-color: #a2e321;
    border: 0px;
    margin-top: 10px;
    border-radius: 10px;
}



.side-list {
    color: #E90015;
}

.list-element {
    padding-left: 5px;
    padding-right: 5px;
    color: #E90015;
    width: 300px;
    text-decoration: none;
}

.list-element:hover {
    background-color: #E90015;
    color: #fff;
    cursor: pointer;
}
.list-element:active {
    background-color: #E90015;
}

body {
    font-family: 'Raleway', sans-serif;
}

h1 {
    text-decoration: underline;
}
.header {
    font-family: 'Cutive Mono', monospace;
    color: #000;
    font-size: 28px;
    top: 10px;
    position: relative;
}

.logo {
    float: left;
    margin: 10px;
    width: 40px;
    height: auto;
}

.contact-us {
    font-size: 14px;
    float: right;
    position: relative;
    top: 30px;
    text-decoration: none;
    font-family: 'Cutive Mono', monospace;
}

.contact-us:hover {
    text-decoration: underline;
}

.container {
    border-bottom: 2px solid #eee;
}
body {
    margin: 0px;
}
